import React, { ChangeEvent, useEffect, useState } from 'react';
import './style.css';
import InputBox from 'src/components/InputBox';
import { idCheckRequest, signInRequest, signUpRequest, telAuthCheckRequest, telAuthRequest } from 'src/apis';
import { IdCheckRequestDto, SignInRequestDto, SignUpRequestDto, TelAuthCheckRequestDto, TelAuthRequestDto } from 'src/apis/dto/request/auth';
import { ResponseDto } from 'src/apis/dto/response';
import { SignInResponseDto } from 'src/apis/dto/response/auth';
import { useCookies } from 'react-cookie';
import { ACCESS_TOKEN, CS_ABSOLUTE_PATH, ROOT_PATH } from 'src/constants';
import { useNavigate } from 'react-router';
import { useSearchParams } from 'react-router-dom';

type AuthPath = '' | '';

interface SnsContainerProps {
    type: AuthPath;
}

// component: SNS    //
function SnsContainer ({ type }: SnsContainerProps) {

    // event handler: SNS     //
    const onSnsButtonClickHandler = (sns: 'kakao' | 'naver') => {
        window.location.href = `http://localhost:4000/api/v1/auth/sns-sign-in/${sns}`;
    };

    // render: SNS     //
    return (
        <div className="sns-container">
            <div className="title">SNS {type}</div>
            <div className="sns-button-container">
                <div className={`sns-button ${type === '' ? 'md ' : ''}kakao`} onClick={() => onSnsButtonClickHandler('kakao')}></div>
                <div className={`sns-button ${type === '' ? 'md ' : ''}naver`} onClick={() => onSnsButtonClickHandler('naver')}></div>
            </div>
        </div>
    );

}

interface AuthComponentProps {
    onPathChange: (path: AuthPath) => void;
}

// component:    //
function SignUp({ onPathChange }: AuthComponentProps) {

    // state: Query Parameter  //
    const [queryParam] = useSearchParams();
    const snsId = queryParam.get('snsId');
    const joinPath = queryParam.get('joinPath');

    // state:     //
    const [name, setName] = useState<string>('');
    const [id, setId] = useState<string>('');
    const [password, setPassword] = useState<string>('');
    const [passwordCheck, setPasswordCheck] = useState<string>('');
    const [telNumber, setTelNumber] = useState<string>('');
    const [authNumber, setAuthNumber] = useState<string>('');

    // state:     //
    const [nameMessage, setNameMessage] = useState<string>('');
    const [idMessage, setIdMessage] = useState<string>('');
    const [passwordMessage, setPasswordMessage] = useState<string>('');
    const [passwordCheckMessage, setPasswordCheckMessage] = useState<string>('');
    const [telNumberMessage, setTelNumberMessage] = useState<string>('');
    const [authNumberMessage, setAuthNumberMessage] = useState<string>('');

    // state:      //
    const [nameMessageError, setNameMessageError] = useState<boolean>(false);
    const [idMessageError, setIdMessageError] = useState<boolean>(false);
    const [passwordMessageError, setPasswordMessageError] = useState<boolean>(false);
    const [passwordCheckMessageError, setPasswordCheckMessageError] = useState<boolean>(false);
    const [telNumberMessageError, setTelNumberMessageError] = useState<boolean>(false);
    const [authNumberMessageError, setAuthNumberMessageError] = useState<boolean>(false);

    // state:    //
    const [isCheckedId ,setCheckedId] = useState<boolean>(false);
    const [isMatchedPassword, setMatchedPassword] = useState<boolean>(false);
    const [isCheckedPassword, setCheckedPassword] = useState<boolean>(false);
    const [isSend, setSend] = useState<boolean>(false);
    const [isCheckedAuthNumber, setCheckedAuthNumber] = useState<boolean>(false);

    // variable: SNS   //
    const isSnsSignUp = snsId !== null && joinPath !== null;

    // variable:    //
    const isComplete = name && id && isCheckedId && password && passwordCheck && isMatchedPassword && isCheckedPassword
        && telNumber && isSend && authNumber && isCheckedAuthNumber;

    // function:    Response   //
    const idCheckResponse = (responseBody: ResponseDto | null) => {
        const message = 
            !responseBody ? '  .' :
            responseBody.code === 'VF' ? '  .' :
            responseBody.code === 'DI' ? '  .' :
            responseBody.code === 'DBE' ? '  .' : 
            responseBody.code === 'SU' ? '  .' : '';

        const isSuccessed = responseBody !== null && responseBody.code === 'SU';
        setIdMessage(message);
        setIdMessageError(!isSuccessed);
        setCheckedId(isSuccessed);
    };

    // function:   Response   //
    const telAuthResponse = (responseBody: ResponseDto | null) => {

        const message = 
            !responseBody ? '  .' :
            responseBody.code === 'VF' ? ' 11 .' :
            responseBody.code === 'DT' ? ' .' :
            responseBody.code === 'TF' ? '  .' :
            responseBody.code === 'DBE' ? '  .' :
            responseBody.code === 'SU' ? ' .' : '';
        
        const isSuccessed = responseBody !== null && responseBody.code === 'SU';
        setTelNumberMessage(message);
        setTelNumberMessageError(!isSuccessed);
        setSend(isSuccessed);

    };

    // function:    Response   //
    const telAuthCheckResponse = (responseBody: ResponseDto | null) => {

        const message =
            !responseBody ? '  .' :
            responseBody.code === 'VF' ? '  .' :
            responseBody.code === 'TAF' ? '  .' :
            responseBody.code === 'DBE' ? '  .' :
            responseBody.code === 'SU' ? ' .' : '';

        const isSuccessed = responseBody !== null && responseBody.code === 'SU';
        setAuthNumberMessage(message);
        setAuthNumberMessageError(!isSuccessed);
        setCheckedAuthNumber(isSuccessed);
    };

    // function:  Response   //
    const signUpResponse = (responseBody: ResponseDto | null) => {
        const message = 
            !responseBody ? '  .' :
            responseBody.code === 'VF' ? '  .' :
            responseBody.code === 'DI' ? ' .' :
            responseBody.code === 'DT' ? ' .' :
            responseBody.code === 'TAF' ? '  .' :
            responseBody.code === 'DBE' ? '  .' : '';
        
        const isSuccessed = responseBody !== null && responseBody.code === 'SU';
        if (!isSuccessed) {
            alert(message);
            return;
        }

        onPathChange('');
    };

    // event handler:     //
    const onNameChangeHandler = (event: ChangeEvent<HTMLInputElement>) => {
        const { value } = event.target;
        setName(value);
    };

    // event handler:     //
    const onIdChangeHandler = (event: ChangeEvent<HTMLInputElement>) => {
        const { value } = event.target;
        setId(value);
        setCheckedId(false);
        setIdMessage('');
    };

    // event handler:     //
    const onPasswordChangeHandler = (event: ChangeEvent<HTMLInputElement>) => {
        const { value } = event.target;
        setPassword(value);

        const pattern = /^(?=.*[a-zA-Z])(?=.*[0-9]).{8,13}$/;
        const isMatched = pattern.test(value);

        const message = (isMatched || !value) ? '' : ',   8 ~ 13 ';
        setPasswordMessage(message);
        setPasswordMessageError(!isMatched);
        setMatchedPassword(isMatched);
    };

    // event handler:      //
    const onPasswordCheckChangeHandler = (event: ChangeEvent<HTMLInputElement>) => {
        const { value } = event.target;
        setPasswordCheck(value);
    };

    // event handler:     //
    const onTelNumberChangeHandler = (event: ChangeEvent<HTMLInputElement>) => {
        const { value } = event.target;
        setTelNumber(value);
        setSend(false);
        setTelNumberMessage('');
    };

    // event handler:     //
    const onAuthNumberChangeHandler = (event: ChangeEvent<HTMLInputElement>) => {
        const { value } = event.target;
        setAuthNumber(value);
        setCheckedAuthNumber(false);
        setAuthNumberMessage('');
    };

    // event handler:       //
    const onIdCheckClickHandler = () => {
        if (!id) return;

        const requestBody: IdCheckRequestDto = {
            userId: id
        };
        idCheckRequest(requestBody).then(idCheckResponse);
    };

    // event handler:       //
    const onTelNumberSendClickHandler = () => {
        if (!telNumber) return;

        const pattern = /^[0-9]{11}$/;
        const isMatched = pattern.test(telNumber);

        if (!isMatched) {
            setTelNumberMessage(' 11 .');
            setTelNumberMessageError(true);
            return;
        }

        const requestBody: TelAuthRequestDto = { telNumber };
        telAuthRequest(requestBody).then(telAuthResponse);
    };

    // event handler:       //
    const onAuthNumberCheckClickHandler = () => {
        if (!authNumber) return;

        const requestBody: TelAuthCheckRequestDto = {
            telNumber, authNumber
        }
        telAuthCheckRequest(requestBody).then(telAuthCheckResponse);
    };

    // event handler:      //
    const onSignUpButtonHandler = () => {
        if (!isComplete) return;

        const requestBody: SignUpRequestDto = {
            name,
            userId: id,
            password,
            telNumber,
            authNumber,
            joinPath: joinPath ? joinPath : 'home',
            snsId
        };
        signUpRequest(requestBody).then(signUpResponse);
    };

    // effect:         //
    useEffect(() => {
        if (!password || !passwordCheck) return;

        const isEqual = password === passwordCheck;
        const message = isEqual ? '' : '  .';
        setPasswordCheckMessage(message);
        setPasswordCheckMessageError(!isEqual);
        setCheckedPassword(isEqual);
    }, [password, passwordCheck]);
    
    // render:     //
    return (
        <div style={{ gap: '16px' }} className="auth-box">
            <div className="title-box">
                <div className="title"></div>
                <div className="logo"></div>
            </div>
            {!isSnsSignUp && <SnsContainer type='' />}
            <div style={{ width: '64px' }} className="divider"></div>

            <div className="input-container">
                <InputBox messageError={nameMessageError} message={nameMessage} value={name} label='' type='text' placeholder=' .' onChange={onNameChangeHandler} />
                <InputBox messageError={idMessageError} message={idMessage} value={id} label='' type='text' placeholder=' .' buttonName=' ' onChange={onIdChangeHandler} onButtonClick={onIdCheckClickHandler} />
                <InputBox messageError={passwordMessageError} message={passwordMessage} value={password} label='' type='password' placeholder=' .' onChange={onPasswordChangeHandler} />
                <InputBox messageError={passwordCheckMessageError} message={passwordCheckMessage} value={passwordCheck} label=' ' type='password' placeholder=' .' onChange={onPasswordCheckChangeHandler} />
                <InputBox messageError={telNumberMessageError} message={telNumberMessage} value={telNumber} label='' type='text' placeholder='- .' buttonName=' ' onChange={onTelNumberChangeHandler} onButtonClick={onTelNumberSendClickHandler} />
                {isSend &&
                <InputBox messageError={authNumberMessageError} message={authNumberMessage} value={authNumber} label='' type='text' placeholder=' 4 .' buttonName=' ' onChange={onAuthNumberChangeHandler} onButtonClick={onAuthNumberCheckClickHandler} />
                }
            </div>

            <div className="button-container">
                <div className={`button ${isComplete ? 'primary' : 'disable'} full-width`} onClick={onSignUpButtonHandler}></div>
                <div className="link" onClick={() => onPathChange('')}></div>
            </div>
        </div>
    )
}

// component:    //
function SignIn({ onPathChange }: AuthComponentProps) {

    // state:   //
    const [cookies, setCookie] = useCookies();

    // state:     //
    const [id, setId] = useState<string>('');
    const [password, setPassword] = useState<string>('');

    // state:     //
    const [message, setMessage] = useState<string>('');

    // function:   //
    const navigator = useNavigate();

    // function:  Response   //
    const signInResponse = (responseBody: SignInResponseDto | ResponseDto | null) => {
        const message = 
            !responseBody ? '  .' :
            responseBody.code === 'VF' ? '   .' :
            responseBody.code === 'SF' ? '   .' : 
            responseBody.code === 'TCF' ? '  .' :
            responseBody.code === 'DBE' ? '  .' : '';
        
        const isSuccessed = responseBody !== null && responseBody.code === 'SU';
        if (!isSuccessed) {
            setMessage(message);
            return;
        }

        const { accessToken, expiration } = responseBody as SignInResponseDto;
        const expires = new Date(Date.now() + (expiration * 1000));
        setCookie(ACCESS_TOKEN, accessToken, { path: ROOT_PATH, expires });

        navigator(CS_ABSOLUTE_PATH);
    };

    // event handler:     //
    const onIdChangeHandler = (event: ChangeEvent<HTMLInputElement>) => {
        const { value } = event.target;
        setId(value);
    };

    // event handler:     //
    const onPasswordChangeHandler = (event: ChangeEvent<HTMLInputElement>) => {
        const { value } = event.target;
        setPassword(value);
    };

    // event handler:      //
    const onSignInButtonHandler = () => {
        if (!id || !password) return;

        const requestBody: SignInRequestDto = {
            userId: id,
            password
        };
        signInRequest(requestBody).then(signInResponse);
    };

    // effect:       //
    useEffect(() => {
        setMessage('');
    }, [id, password]);

    // render:     //
    return (
        <div className="auth-box">
            <div className="title-box">
                <div className="title"></div>
                <div className="logo"></div>
            </div>
            <div className="input-container">
                <InputBox value={id} onChange={onIdChangeHandler} message='' messageError type='text' label='' placeholder=' .' />
                <InputBox value={password} onChange={onPasswordChangeHandler} message={message} messageError type='password' label='' placeholder=' .' />
            </div>
            <div className="button-container">
                <div className="button primary full-width" onClick={onSignInButtonHandler}></div>
                <div className="link" onClick={() => onPathChange('')}></div>
            </div>
            <div style={{ width: '64px' }} className="divider"></div>
            <SnsContainer type='' />
        </div>
    );

}

// component:    //
export default function Auth() {

    // state: Query Parameter  //
    const [queryParam] = useSearchParams();
    const snsId = queryParam.get('snsId');
    const joinPath = queryParam.get('joinPath');
    // state:    //
    const [path, setPath] = useState<AuthPath>('');

    // event handler:     //
    const onPathChangeHandler = (path: AuthPath) => {
        setPath(path);
    };

    // effect:   Query Param snsId joinPath     //
    useEffect(() => {
        if (snsId && joinPath) setPath('');
    }, []);

    // render:     //
    return (
        <div id="auth-wrapper">
            <div className="auth-image"></div>
            <div className="auth-container">
                {path === '' ? 
                <SignIn onPathChange={onPathChangeHandler} /> :
                <SignUp onPathChange={onPathChangeHandler} />
                }
            </div>
        </div>
        
    );

}